import { cloneObj, getRandomValue, getUID } from "./otherFunctions"
import UnitStates from "../game resources/unitStates"
import * as CHECK_EFFECT from "../functions/checkEffect"


export function setUnitStats(unit, statsArr, valuesArr, bmap, setBmap, setUnits) {
    let newUnit = cloneObj(unit), updatedValues = {}
    statsArr.forEach((stat, index) => {
        newUnit[stat] = valuesArr[index]
        updatedValues[stat] = valuesArr[index]
    })

    //   -  
    if (unit.x !== newUnit.x || unit.y !== newUnit.y) {
        let newBmap = bmap.map(arr => arr.slice())
        newBmap[unit.y][unit.x].unitID = null
        newBmap[newUnit.y][newUnit.x].unitID = newUnit.unitID
        setBmap(newBmap)
    }

    /*
    // 
    setUnits(prevState => prevState.map(item => {
        return (item.unitID === unit.unitID) ? {...item, ...updatedValues} : item
    }))*/

    setUnits(prevState => prevState.map(item => {
        if (item !== null && item.unitID === unit.unitID) {
            statsArr.forEach((stat, index) => item[stat] = parseInt(valuesArr[index]))
            return { ...item }
        } 
        else return item
    }))
}

export function modifyUnitStats(unit, statsArr, valuesArr, setUnits, setQueue) {
    setUnits(prevState => prevState.map(item => {
        if (item !== null && item.unitID === unit.unitID) {
            statsArr.forEach((stat, index) => {
                let value = parseInt(valuesArr[index])
                if (stat === 'AC') item['physRes'] += value * 4
                item[stat] = item[stat] + value 
                //    
                if ((stat === 'HP' || stat === 'Mana' || stat === 'WP') && value > 0) {
                    if (item[stat] > item['max' + stat]) item[stat] = item['max' + stat]
                }
            })
            //  
            if (item.alive && item['HP'] === 0) killUnit(item.unitID, setQueue, setUnits)
            return { ...item }
        } 
        else return item
    }))
}

// -,        (     + )
export function killUnit(unitID, setQueue, setUnits) {
    setUnits(prevState => prevState.map(unit => {
        if (unit !== null && unit.unitID === unitID) return {...unit, alive: false}
        else return unit
    }))
    setQueue(prevState => prevState.filter(item => item !== unitID))
}

// 
export function modifySurface(targetPos, setBmap, incompatibleTerrains, surfacesTable) {
    let i = targetPos.i, j = targetPos.j 
        setBmap(prevState => prevState.map((row, rowNum) => {
            return row.map((cell, colNum) => {
                if (i === rowNum && j === colNum) {
                    if (!incompatibleTerrains.includes(cell.subtype)) 
                        //     ,      
                        return {...cell, surface: surfacesTable[cell.surface]}
                    else return cell
                } else return cell
            })
        }))
}


// 


export function checkStatus(status, unit) {
    if (unit.statuses.some(item => item === status)) 
        return true
    else return false
}

//  / 

//    /     / 
export function getPrimaryValue(dmg, dmgType, caster) {
    let {str, int, will, intMul, strMul, agiMul} = caster
    //multiplier -     ,   
    //statBonus -    
    //statMultiplier -    
    let actDmg = [0, 0], multiplier = 1, statExtraBonus = 0, statMultiplier = 0.02
    // 
    if (dmgType[1] === 'dmg') {
        if (dmgType[0] === 'phys') multiplier = 1 + str * statMultiplier
        else if (dmgType[0] === 'pure' || dmgType[0] === 'poison' || dmgType[0] === 'mana') multiplier = 1
        else if (dmgType[0] === 'will') multiplier = 1 + will * statMultiplier
        else multiplier = 1 + int * statMultiplier//  /  /   
    }
    // 
    else if (dmgType[1] === 'heal') {
        if (dmgType[2] === null) multiplier = 1
        else if (dmgType[2] === 'int') multiplier = 1 + int * statMultiplier
        else if (dmgType[2] === 'will') multiplier = 1 + will * statMultiplier
        else multiplier = 1
    }
    //     
    if (dmg[3] === 'intMul') statExtraBonus = dmg[2] * intMul
    actDmg[0] = Math.round(dmg[0] * multiplier) + statExtraBonus
    actDmg[1] = (dmg[1] !== null) ? Math.round(dmg[1] * multiplier) + statExtraBonus : null
    return actDmg
}

//       / 
export function getActualDmg(dmgSource, caster, targetUnit, stat, dmgRecalulates = true) {
    let statMul = null, statMulValue = 0, dmgBonus = 0, {dmg, dmgType} = dmgSource
    //     -
    if (dmgSource.name === 'Death ripple' && caster.unitID === targetUnit.unitID) return [0, 0]
    //    ( )
    if (!CHECK_EFFECT.canBeDamaged(targetUnit)) return [0, 0]
    if (stat === 'HP' && checkCondition('Aegis', targetUnit)) return [0, 0]
    if (stat === 'WP' && CHECK_EFFECT.hasMindProtection(targetUnit)) return [0, 0]

    //    
    if (dmg.length > 2 && caster !== null) {
        statMulValue = dmg[2]
        statMul = dmg[3]
        dmgBonus = statMulValue * caster[statMul]
    } 
    //  (  ,    )
    let actDmg = (dmgRecalulates) ? getPrimaryValue(dmg, dmgType, caster) : dmg
    let minDmg = actDmg[0] + dmgBonus
    let maxDmg = (actDmg[1] !== null) ? actDmg[1] + dmgBonus : null

    if (stat === 'HP') {
        //     
        if (caster !== null && dmgRecalulates) {
            let multiBonus = 1
            if (checkCondition('Blood lust', caster) && checkState('Bleeding', targetUnit)) multiBonus += 0.15
            minDmg = minDmg * multiBonus
            maxDmg = (maxDmg !== null) ? maxDmg * multiBonus : null
        }
        
        //       
        let resMultiplier
        if (dmgType[0] === 'poison' || dmgType[0] === 'pure') resMultiplier = 1
        else resMultiplier = 1 - (targetUnit[dmgType[0] + 'Res'] / 100)
        if (resMultiplier < 0) resMultiplier = 0
        minDmg = Math.floor(minDmg * resMultiplier)
        maxDmg = (maxDmg !== null) ? Math.floor(maxDmg * resMultiplier) : null
    }
    else if (stat === 'WP') {
        let willResMultiplier = 0.5//      1 
        let targetWillRes = Math.floor(targetUnit['will'] * willResMultiplier)
        let resMultiplier = 1 - (targetWillRes / 100)
        if (resMultiplier < 0) resMultiplier = 0
        minDmg = Math.floor(minDmg * resMultiplier)
        maxDmg = (maxDmg !== null) ? Math.floor(maxDmg * resMultiplier) : null
    }
    //   / 
    if (caster !== null) {
        if (dmgRecalulates && checkCondition("Bless", caster)) minDmg = maxDmg
        else if (dmgRecalulates && checkCondition("Curse", caster)) maxDmg = minDmg
    }
    return [minDmg, maxDmg]
}

//   
function getResultDmgValue(dmgSource, caster, targetUnit, stat, setUnits, dmgRecalulates = true) {
    //    ( )
    if (stat === 'HP' && checkCondition('Aegis', targetUnit)) {
        checkAndRemoveCondition('Aegis', targetUnit, setUnits)
        return 0
    }
    let actDmg = getActualDmg(dmgSource, caster, targetUnit, stat, dmgRecalulates)
    return (actDmg[1] !== null) ? getRandomValue(actDmg[0], actDmg[1]) : actDmg[0]
}

//      
export function getActualHeal(healSource, caster, targetUnit, stat, dmgRecalulates = true) {
    if (!CHECK_EFFECT.isHealable(targetUnit)) return [0, 0]
    let healMultiplier = 1.0, {dmg, dmgType} = healSource

    //  (   -   )
    let actHeal = (dmgRecalulates) ? getPrimaryValue(dmg, dmgType, caster) : dmg
    let minHeal = actHeal[0]
    let maxHeal = (actHeal[1] !== null) ? actHeal[1] : null

    if (stat === 'HP') {
        //    
        if (checkState('Diseased', targetUnit)) healMultiplier *= 0.5
        if (checkState('Greater poison', targetUnit)) healMultiplier *= 0.5
        minHeal = minHeal * healMultiplier
        maxHeal = (maxHeal !== null) ? maxHeal * healMultiplier : null

        //   / 
        if (caster !== null) {
            if (dmgRecalulates && checkCondition("Bless", caster)) minHeal = maxHeal
            else if (dmgRecalulates && checkCondition("Curse", caster)) maxHeal = minHeal
        }
    }
    
    return [minHeal, maxHeal]
}

//   
function getResultHealValue(healSource, caster, targetUnit, stat, dmgRecalulates = true) {
    let actHeal = getActualHeal(healSource, caster, targetUnit, stat, dmgRecalulates)
    return (actHeal[1] !== null) ? getRandomValue(actHeal[0], actHeal[1]) : actHeal[0]
}

//   /     /   
//dmgRecalulates: false -   ,    ,   , true - 
export function getFinalValue(dmgSource, caster, targetUnit, stat, maxStat, setUnits, dmgRecalulates = true) {
    let {dmg, dmgType} = dmgSource
    if (dmg[0] === 0 && (dmg[1] === null || dmg[1] === 0)) return 0//   0
    if (dmgType[1] === 'dmg') {
        let actDmg = getResultDmgValue(dmgSource, caster, targetUnit, stat, setUnits, dmgRecalulates)
        //     1
        if (stat === 'HP' && !CHECK_EFFECT.canDie(targetUnit)) {
            return (targetUnit['HP'] - actDmg >= 1) ? actDmg : targetUnit[stat] - 1
        }
        else return (targetUnit[stat] - actDmg >= 0) ? actDmg : targetUnit[stat]
    }
    else if (dmgType[1] === 'heal') {
        if (targetUnit[stat] < targetUnit[maxStat]) {
            let actHeal = getResultHealValue(dmgSource, caster, targetUnit, stat, dmgRecalulates)
            return (targetUnit[stat] + actHeal <= targetUnit[maxStat]) ? actHeal : targetUnit[maxStat] - targetUnit[stat]
        }
        else return 0
    }
}

