//    1 
export function decreaseConditionDuration(condition, unit, setUnits) {
    setUnits(prevState => prevState.map(item => {
        return (item !== null && item.unitID === unit.unitID) ? {...item, 
            conditions: item.conditions.map(cond => {
                if (condition.spell.name === cond.spell.name && condition.UID === cond.UID) {
                    return {...cond, duration: cond.duration - 1}
                } else return cond
            })} : item
    }))
}

// 
export function addNewCondition(spell, source, duration, unit, effect, setUnits) {
    let newCondition = {
        spell: cloneObj(spell),
        source: cloneObj(source),
        duration,
        effect,
        cumulative: (effect.cumulative === true) ? true : false,// 
        dispellable: (effect.dispellable === false) ? false : true,//  
        delayedEffect: (effect.delayedEffect) ? effect.delayedEffect : null,//  (    -)
        positiveness: effect.positiveness,
        UID: getUID()
    }

    //        
    if (!newCondition.cumulative && checkCondition(newCondition.spell.name, unit)) {
        setUnits(prevState => prevState.map(item => {
            return (item !== null && item.unitID === unit.unitID) ? {...item, conditions: item.conditions.map(cond => {
                //            (  )
                //  ,     , ..  
                if (cond.spell.name === newCondition.spell.name && cond.duration <= newCondition.duration) {
                    return {...cond, source: newCondition.source, duration: newCondition.duration}
                } else return cond
            })} : item
        }))
    }
    else {
        if (newCondition.effect.statsArr.length) {
            modifyUnitStats(unit, 
                effect.statsArr, 
                effect.valuesArr,
                setUnits)
        }    

        setUnits(prevState => prevState.map(item => {
            return (item !== null && item.unitID === unit.unitID) ? {...item, conditions: [...item.conditions, {...newCondition}]} : item
        }))
    }

    //   
    if (newCondition.effect.state) addNewState(newCondition.effect.state, duration, source, unit, setUnits)
}

// 
export function removeCondition(condition, unit, setUnits, flag = "REMOVE_DEFAULT") {
    //   (  )
    if (condition.effect.statsArr.length) {
        let statsArr = condition.effect.statsArr
        let negativeValuesArr = condition.effect.valuesArr.map((item, index) => {
            //         (  )
            /*if (statsArr[index] === "MP" || statsArr[index] === "AP") {
                //    "" 
                if (flag === "REMOVE_DEFAULT") return 0
                //      
                else if (flag === "REMOVE_BY_DURATION") return -parseInt(item)
            }
            else */return -parseInt(item)
        })
        modifyUnitStats(unit, 
            condition.effect.statsArr, 
            negativeValuesArr,
            setUnits)
    }

    //    -    (   !!!!!!!!!!!!!)
    if (condition.effect.state) removeState(condition.effect.state, unit, setUnits)
    
    setUnits(prevState => prevState.map(item => {
        return (item !== null && item.unitID === unit.unitID) ? {...item, 
            conditions: item.conditions.filter(cond => !(cond.spell.name === condition.spell.name && cond.UID === condition.UID))} : item
    }))
}

//   
export function checkCondition(conditionName, unit) {
    if (unit.conditions.some(item => item.spell.name === conditionName)) 
        return true
    else return false
}

// ,  , 
export function checkAndRemoveCondition(conditionName, unit, setUnits) {
    unit.conditions.forEach(item => {
        if (item.spell.name === conditionName) {
            removeCondition(item, unit, setUnits)
            return
        }
    })
}

//

// 
export function addNewState(state, duration, source, unit, setUnits) {
    let newState = {
        name: state.name,
        runame: state.runame,
        img: state.img,
        description: state.description,
        type: state.type,
        dispellable: state.dispellable,
        duration,
        positiveness: state.positiveness,
        source,
        effect: state.effect,
        delayedEffect: (state.delayedEffect) ? state.delayedEffect : null,//  (    -)
        UID: getUID()
    }

    // -      
    function isPriorStat(stat) {
        if (stat === 'vit' || stat === 'wis' || stat === 'str' || 
            stat === 'agi' || stat === 'int' || stat === 'will') return true
        else return false
    }

    //     -     
    //(   ,  )
    if (newState.name !== "Lesser poison" && newState.name !== "Greater poison" && checkState(newState.name, unit)) {
        setUnits(prevState => prevState.map(item => {
            return (item !== null && item.unitID === unit.unitID) ? {...item, states: item.states.map(st => {
                //            (  )
                //  ,     , ..  
                if (st.name === newState.name && st.duration <= newState.duration) {
                    return {...st, source: newState.source, duration: newState.duration}
                } else return st
            })} : item
        }))
    } else {
        let resultValuesArr = [...newState.effect.valuesArr]
        if (newState.effect.statsArr.length) {
            let statsArr = newState.effect.statsArr, valuesArr = newState.effect.valuesArr
            statsArr.forEach((stat, index) => {
                //          -   
                if (isPriorStat(stat) && valuesArr[index].slice(-1) === '%') {
                    resultValuesArr[index] = Math.ceil(parseInt(valuesArr[index]) / 100 * unit[stat])
                }
            })
            modifyUnitStats(unit, 
                newState.effect.statsArr, 
                resultValuesArr,
                setUnits)
        }
    
        setUnits(prevState => prevState.map(item => {
            // 
            //return (item.unitID === unit.unitID) ? {...item, states: [...item.states, {...newState}]} : item
            //valuesArr  ,     %-,      
            return (item !== null && item.unitID === unit.unitID) ? 
            {...item, states: [...item.states, {...newState, effect: {...newState.effect, valuesArr: resultValuesArr}}]}
            : item
        }))
    }
}

//   
export function checkState(stateName, unit) {
    if (unit.states.some(item => item.name === stateName)) 
        return true
    else return false
}

// ,  , 
export function checkAndRemoveState(stateName, unit, setUnits) { 
    unit.states.forEach(st => {
        if (st.name === stateName) {
            removeState(st, unit, setUnits)
            return
        }
    })
}

//    1 
export function decreaseStateDuration(state, unit, setUnits) {
    //   
    if (state.duration === "permanent") return

    setUnits(prevState => prevState.map(item => {
        return (item !== null && item.unitID === unit.unitID) ? {...item, 
            states: item.states.map(st => {
                if (state.name === st.name && state.UID === st.UID) {
                    return {...st, duration: st.duration - 1}
                } else return st
            })} : item
    }))
}

// 
export function removeState(state, unit, setUnits) {
    //   
    if (state.duration === "permanent") return

    //   (  )
    if (state.effect.statsArr.length) {
        let negativeValuesArr = state.effect.valuesArr.map(item => -parseInt(item))
        modifyUnitStats(unit, 
            state.effect.statsArr, 
            negativeValuesArr,
            setUnits)
    }
    
    setUnits(prevState => prevState.map(item => {
        return (item !== null && item.unitID === unit.unitID) ? {...item, 
            states: item.states.filter(st => !(state.name === st.name && state.UID === st.UID))} : item
    }))
}


//


export function summonCreature(creature, caster, targetPos, setUnits, setBmap, setQueue) {
    let x = targetPos.j, y = targetPos.i 
    setUnits(prevState => [...prevState, creature])
    setBmap(prevState => prevState.map((row, i) => {
        return row.map((cell, j) => {
            if (i === y && j === x) {
                return {...cell, unitID: creature.unitID}
            } else return cell
        })
    }))
    
    setQueue(prevState => {
        let casterId = prevState.indexOf(caster.unitID)
        return [...prevState.slice(0, casterId + 1), creature.unitID, ...prevState.slice(casterId + 1)]
    })
}