import { checkStatus, checkCondition, checkState} from "./battleFunctions"



//  


export function canBeMoved(unit) {
    if (checkState('Gravity', unit) 
        || checkState('Petrified', unit)
        || checkState('Frozen', unit)
        || checkState('Entangled', unit)) return false
    else return true
}

export function canUseDisplacementSpells(unit) {
    if (checkState('Gravity', unit) || checkState('Entangled', unit)) return false
    else return true
}

export function dontHaveMovementPenalty(unit) {
    if (checkState("Flight", unit) && !checkState("Gravity", unit)) return true
    else if (checkState("Offroad stride", unit)) return true
    else return false
}

export function hasSpellInvul(unit) {
    if (checkCondition('Antimagic', unit)) return true
    else return false
}

export function hasMindProtection(unit) {
    if (checkCondition('Ataraxia', unit) 
        || checkCondition('Unity with nature', unit)
        || checkCondition('Geostasis', unit)) return true
    else return false
}

export function isHealable(unit) {
    if (checkState('Unhealable', unit) || checkStatus('undead', unit)) return false
    else return true
}

//   
export function skipsTurn(unit) {
    if (checkState('Paralyzed', unit) || checkState('Scared', unit) 
        || checkState('Asleep', unit) || checkState('Petrified', unit)
        || checkState('Frozen', unit) || checkState('Charmed', unit)
        || checkCondition('Geostasis', unit) || checkState('Stunned', unit)) return true
    else return false
}

//   
export function canBeAPRaped(unit) {
    if (checkCondition('Hot blood', unit)
        || checkCondition('Geostasis', unit)) 
        return false
    else return true
}

//  
export function canDie(unit) {
    if (checkCondition('Life on the edge', unit)) 
        return false
    else return true
}

//   
export function canBeDamaged(unit) {
    if (checkCondition('Geostasis', unit)) 
        return false
    else return true
}


//    


export function isSummon(unit) {
    if (checkStatus('summon', unit)) return true
    else return false
}

export function canBePoisoned(unit) {
    if (checkStatus('undead', unit)
    || checkStatus('reptile', unit)
    || checkCondition('Reptile skin', unit)
    || checkCondition('Geostasis', unit)) 
        return false
    else return true
}

export function canBurn(unit) {
    if (checkCondition('Stone skin', unit)
        || checkState('Petrified', unit) 
        || checkState('Geostasis', unit) 
        || checkState('Frozen', unit)) 
        return false
    else return true
}

export function canBeWet(unit) {
    if (checkCondition('Hot blood', unit)
    || checkCondition('Geostasis', unit)) 
        return false
    else return true
}

export function canBeChilled(unit) {
    if (checkCondition('Hot blood', unit)
    || checkCondition('Geostasis', unit)) 
        return false
    else return true
}

export function canBeFrozen(unit) {
    if (checkCondition('Hot blood', unit)
    || checkCondition('Geostasis', unit)) 
        return false
    else return true
}

export function canBePetrified(unit) {
    if (checkCondition('Stamina', unit)
    || checkCondition('Geostasis', unit)) 
        return false
    else return true
}

export function canBeWeakened(unit) {
    if (checkStatus('undead', unit) 
        || checkCondition('Hour of power', unit)
        || checkCondition('Burst of strength', unit) 
        || checkCondition('Unlimited health', unit)
        || checkCondition('Geostasis', unit)) 
        return false
    else return true
}

export function canBeDiseased(unit) {
    if (checkStatus('undead', unit) 
        || checkCondition('Unlimited health', unit)
        || checkCondition('Geostasis', unit)) 
        return false
    else return true
}

export function canBeAged(unit) {
    if (checkStatus('undead', unit) 
        || checkCondition('Unlimited health', unit)
        || checkCondition('Geostasis', unit)) 
        return false
    else return true
}

export function canBleed(unit) {
    if (checkStatus('undead', unit) 
        || checkCondition('Unlimited health', unit)
        || checkCondition('Stone skin', unit)
        || checkState('Petrified', unit) 
        || checkState('Geostasis', unit) 
        || checkState('Frozen', unit)) return false
    else return true
}

export function canBeAsleep(unit) {
    if (checkStatus('undead', unit) 
        || checkCondition('Ataraxia', unit)
        || checkCondition('Geostasis', unit)) return false
    else return true
}