import React from "react"
import '../styles/styles.css'
import { getPrimaryValue } from './functions/battleFunctions'

import img_fire from './game resources/img/icons/fire magic.png';
import img_water from './game resources/img/icons/water magic.png';
import img_air from './game resources/img/icons/air magic.png';
import img_earth from './game resources/img/icons/earth magic.png';
import img_necro from './game resources/img/icons/necromancy.png';
import img_nature from './game resources/img/icons/nature magic.png';
import img_body from './game resources/img/icons/body magic.png';
import img_mind from './game resources/img/icons/mind magic.png';
import img_light from './game resources/img/icons/light magic.png';
import img_dark from './game resources/img/icons/dark magic.png';
import img_warfare from './game resources/img/icons/warfare.png';
import img_huntsman from './game resources/img/icons/huntsman skills.png';
import img_special from './game resources/img/icons/special skills.png';
import img_KAPPA from './game resources/img/icons/KAPPA.png';






export default function SpellBook(props) { 
    let [chosenSchool, setChosenSchool] = React.useState('fire')
    let spells = props.availableSpells
    let magicSchoolsMap = createSpellsMap(spells, props.isPlayersTurn, props.curUnit, props.activeSpells, props.setActiveSpells)
    let curSpells = magicSchoolsMap.get(chosenSchool)
    let lvl1Spells = (curSpells[0].length > 0) ? <div className="sp-book--sp-level"><div className="sp-book--sp-level--num">I</div>{curSpells[0]}</div> : <></>
    let lvl2Spells = (curSpells[1].length > 0) ? <div className="sp-book--sp-level"><div className="sp-book--sp-level--num">II</div>{curSpells[1]}</div> : <></>
    let lvl3Spells = (curSpells[2].length > 0) ? <div className="sp-book--sp-level"><div className="sp-book--sp-level--num">III</div>{curSpells[2]}</div> : <></>
    let lvl4Spells = (curSpells[3].length > 0) ? <div className="sp-book--sp-level"><div className="sp-book--sp-level--num">IV</div>{curSpells[3]}</div> : <></>
    
    return(
        <div className="spell-book">
            <div className="spell-book--schools-block">
                <img className="spell-book--school-button" src={img_fire} alt=" " onClick={()=>setChosenSchool('fire')}/>
                <img className="spell-book--school-button" src={img_water} alt=" " onClick={()=>setChosenSchool('water')}/>
                <img className="spell-book--school-button" src={img_air} alt=" " onClick={()=>setChosenSchool('air')}/>
                <img className="spell-book--school-button" src={img_earth} alt=" " onClick={()=>setChosenSchool('earth')}/>
                <img className="spell-book--school-button" src={img_necro} alt="" onClick={()=>setChosenSchool('necro')}/>
                <img className="spell-book--school-button" src={img_nature} alt=" " onClick={()=>setChosenSchool('nature')}/>
                <img className="spell-book--school-button" src={img_body} alt=" " onClick={()=>setChosenSchool('body')}/>
                <img className="spell-book--school-button" src={img_mind} alt=" " onClick={()=>setChosenSchool('mind')}/>
                <img className="spell-book--school-button" src={img_light} alt=" " onClick={()=>setChosenSchool('light')}/>
                <img className="spell-book--school-button" src={img_dark} alt=" " onClick={()=>setChosenSchool('dark')}/>
                <img className="spell-book--school-button" src={img_warfare} alt=" " onClick={()=>setChosenSchool('warfare')}/>
                <img className="spell-book--school-button" src={img_huntsman} alt=" " onClick={()=>setChosenSchool('huntsman')}/>
                <img className="spell-book--school-button" src={img_special} alt=" " onClick={()=>setChosenSchool('special')}/>
                <img className="spell-book--school-button" src={img_KAPPA} alt="" onClick={()=>setChosenSchool('TEST')}/>
            </div>   
            <div className="spell-book--spells-block">
                {lvl1Spells}
                {lvl2Spells}
                {lvl3Spells}
                {lvl4Spells}
            </div>
        </div>
    )
}



function SpellItem(props) {
    let [isHovered, setIsHovered] = React.useState(false)
    let itemStyle = "skill--item margin-right"

    function addActiveSpell() {
        setIsHovered(false)
        let spellAdded = false, alreadyAdded = false
        //,     
        props.activeSpells.forEach(item => {
            if (item !== null && item.name === props.spellInfo.name) {
                alreadyAdded = true
                return
            }
        })
        if (alreadyAdded) return
        props.setActiveSpells(prevState => prevState.map(item => {
            if (!spellAdded && item === null) {
                spellAdded = true
                return props.spellInfo
            }
            return item
        }))
    }

    return (
        <div className={itemStyle} id={props.id}>
            {(isHovered && props.isPlayersTurn) && <SpellBookTooltip spellInfo={props.spellInfo} curUnit={props.curUnit}/>}
            <img src={props.src} alt=""
                 onMouseOver={()=>setIsHovered(true)}
                 onMouseOut={()=>setIsHovered(false)}
                 onMouseDown={()=>addActiveSpell()}
                 draggable={false}/>
        </div>
    )
}



function SpellBookTooltip(props) {
    let {name, runame, magicSchool, level, description, img, cd, dmg, dmgPerTurn, dmgType, duration, manacost, apcost, range, area, 
        target, LOS, castsPerTurn, castsPerFight, requirment} = props.spellInfo
       
    let actDmg, minDmg, maxDmg
    if (dmg !== null) {
        actDmg = getPrimaryValue(dmg, dmgType, props.curUnit)
        minDmg = actDmg[0]
        maxDmg = actDmg[1]
    }
    let actDmgPerTurn, minDmgPerTurn, maxDmgPerTurn
    if (dmgPerTurn !== null) {
        actDmgPerTurn = getPrimaryValue(dmgPerTurn, dmgType, props.curUnit)
        minDmgPerTurn = actDmgPerTurn[0]
        maxDmgPerTurn = actDmgPerTurn[1]
    }
    //   (,   )
    let range2span = range[2] ? (<span>, {areaTrans[range[2]]}</span>) : <></>
    let dmgSpan = <></>
    if (dmg !== null) dmgSpan = (maxDmg !== null) ? (<span>{minDmg}-{maxDmg}</span>) : (<span>{minDmg}</span>)

    let tooltipStyle = "skill--tooltip"
    let tooltipPos = " spell-book-pos"

    //     
    return(
        <div className={tooltipStyle + tooltipPos}>
            <div className="tooltip--content">
                <div className="tooltip--header">
                    <p className="tooltip--header-name">{runame}</p>
                    <img className="tooltip--header-image" src={img} alt=""/>
                </div>
                {(dmg !== null) && 
                ((dmgType[1] !== 'heal') ? 
                <p>: {dmgSpan}</p> : 
                <p>: {dmgSpan}</p>)}
                {(dmgPerTurn !== null) && <p> : {minDmgPerTurn}-{maxDmgPerTurn}</p>}
                {(dmgType !== null && dmgType[1] !== 'heal') && <p> : {dmgTypeTrans[dmgType[0]]}</p>}
                {(duration !== null) && <p>: {duration}</p>}
                <p>: {manacost}</p>
                <p>: {apcost}</p>
                {(cd !== 0) && <p>: {cd}</p>}
                {(castsPerTurn !== null) && <p>  : {castsPerTurn}</p>}
                {(range[1] !== 0) ? <p>: {range[0]}-{range[1]}{range2span}</p> : <p>: 0</p>}
                <p>: {areaTrans[area[0]]}</p>     
                {(area[1] && area[1] !== 99) && <p> : {area[1]}</p>}
                <p>: {targetTrans[target]}</p>
                <p> : {LOS ? '' : ''}</p>
                {(castsPerFight !== null) && <p>  : {castsPerFight}</p>}
                {(requirment !== null) && <p>: {requirment}</p>}
                <p className="tooltip--description">{description}</p>
            </div>
        </div>
    )
}


//
let dmgTypeTrans = {
    'phys': '',
    'fire': '',
    'water': '',
    'air': '',
    'earth': '',
    'light': '',
    'dark': '',
    'will': '',
    'poison': '',
    'pure': '',
}

let targetTrans = {
    'any': '',
    'enemy': '',
    'ally': '',
    'notarget': ''
}

let areaTrans = {
    'solo': ' ',
    'allMap': ' ',
    'square': '',
    'cross': '',
    'ring': '',
    'rhombus': '',
    'linear': ' ',
}


function createSpellsMap(spells, isPlayersTurn, curUnit, activeSpells, setActiveSpells) {
    let fireSpells = [[], [], [], []], 
        waterSpells = [[], [], [], []], 
        airSpells = [[], [], [], []], 
        earthSpells = [[], [], [], []], 
        necroSpells = [[], [], [], []], 
        natureSpells = [[], [], [], []], 
        bodySpells = [[], [], [], []], 
        mindSpells = [[], [], [], []], 
        lightSpells = [[], [], [], []], 
        darkSpells = [[], [], [], []],
        warfareSpells = [[], [], [], []], 
        huntsmanSpells = [[], [], [], []], 
        specialSpells = [[], [], [], []], 
        TEST_SPELLS = [[], [], [], []]

    Object.keys(spells).forEach((key, i) => {
        let spell = spells[key]
        let src = spell.img
        let spellItem = <SpellItem key={i} id={i} src={src} 
                                   spellInfo={spell}
                                   isPlayersTurn={isPlayersTurn}
                                   curUnit={curUnit}
                                   activeSpells={activeSpells}
                                   setActiveSpells={setActiveSpells}/> 
        let spLvl = spell.level - 1

        //   
        if (spell.magicSchool === 'fire magic') fireSpells[spLvl].push(spellItem)
        else if (spell.magicSchool === 'water magic') waterSpells[spLvl].push(spellItem)
        else if (spell.magicSchool === 'air magic') airSpells[spLvl].push(spellItem)
        else if (spell.magicSchool === 'earth magic') earthSpells[spLvl].push(spellItem)
        else if (spell.magicSchool === 'necromancy') necroSpells[spLvl].push(spellItem)
        else if (spell.magicSchool === 'nature magic') natureSpells[spLvl].push(spellItem)
        else if (spell.magicSchool === 'body magic') bodySpells[spLvl].push(spellItem)
        else if (spell.magicSchool === 'mind magic') mindSpells[spLvl].push(spellItem)
        else if (spell.magicSchool === 'light magic') lightSpells[spLvl].push(spellItem)
        else if (spell.magicSchool === 'dark magic') darkSpells[spLvl].push(spellItem)
        else if (spell.magicSchool === 'warfare') warfareSpells[spLvl].push(spellItem)
        else if (spell.magicSchool === 'huntsman') huntsmanSpells[spLvl].push(spellItem)
        else if (spell.magicSchool === 'special') specialSpells[spLvl].push(spellItem)

        else if (spell.magicSchool === 'TEST SPELL') TEST_SPELLS[spLvl].push(spellItem)
    })

    let magicSchoolsMap = new Map();
    magicSchoolsMap.set("fire", fireSpells)
    magicSchoolsMap.set("water", waterSpells)
    magicSchoolsMap.set("air", airSpells)
    magicSchoolsMap.set("earth", earthSpells)
    magicSchoolsMap.set("necro", necroSpells)
    magicSchoolsMap.set("nature", natureSpells)
    magicSchoolsMap.set("body", bodySpells)
    magicSchoolsMap.set("mind", mindSpells)
    magicSchoolsMap.set("light", lightSpells)
    magicSchoolsMap.set("dark", darkSpells)
    magicSchoolsMap.set("warfare", warfareSpells)
    magicSchoolsMap.set("huntsman", huntsmanSpells)
    magicSchoolsMap.set("special", specialSpells)
    magicSchoolsMap.set("TEST", TEST_SPELLS)

    return magicSchoolsMap
}