import React from "react"
import * as PIXI from "pixi.js";
import { Stage, Container, Sprite, Text } from '@pixi/react'

import '../styles/styles.css'
import SpellBook from "./SpellBook"
import BattleField from './BattleField'
import BattleMenu from './BattleMenu'
import BattleMaps from "./game resources/battle_maps"
import Spells from './game resources/spells'
import Heroes from './game resources/heroes'
import UnitStates from "./game resources/unitStates"
import { setUnitStats, modifyUnitStats, killUnit, decreaseConditionDuration, removeCondition, 
         checkState, removeState, decreaseStateDuration, checkCondition, 
         getPrimaryValue, getActualDmg, getActualHeal } from "./functions/battleFunctions"
import { getRandomValue, createMatrix, cloneObj, countArea, mergeStatChanges } from "./functions/otherFunctions"
import { findPath } from "./functions/AStarAlgo"
import { drawLine } from "./functions/BresenhamAlgo"
import * as CHECK_EFFECT from "./functions/checkEffect"

import img_deepWater from './game resources/img/terrain/deep water.png';
import img_spruce from './game resources/img/terrain/spruce.png';
import img_grass from './game resources/img/terrain/grass.png';
import img_sand from './game resources/img/terrain/sand.png';
import img_shallowWater from './game resources/img/terrain/shallow water.png';

import img_waterSurface from './game resources/img/surface/waterSurface.png';
import img_oilSurface from './game resources/img/surface/oilSurface.png';


let terrainObj = {
    grass: img_grass,
    sand: img_sand,
    ['shallow water']: img_shallowWater,
}

let obstaclesObj = {
    ['deep water']: img_deepWater,
    spruce: img_spruce,
}

let surfacesObj = {
    ['water surface']: img_waterSurface,
    ['oil surface']: img_oilSurface,
}

let NO_TINT = '#FFFFFF'
let DARK_TINT = '#555555'//AAAAAA
let SHADOW_TINT = '#AAAAAA'//AAAAAA
let BRIGHT_TINT = '#E1CA3F'//E1CA3F
let FOCUS_TINT = '#4169E1'

let DAMAGE_HP_TEXT_COLOR = 'rgb(255, 255, 255)'
let DAMAGE_WP_TEXT_COLOR = 'rgb(255, 255, 255)'
let DAMAGE_MANA_TEXT_COLOR = 'rgb(255, 255, 255)'
let HEAL_HP_TEXT_COLOR = 'rgb(30, 220, 255)'
let HEAL_WP_TEXT_COLOR = 'rgb(30, 220, 255)'
let HEAL_MANA_TEXT_COLOR = 'rgb(30, 220, 255)'


export default function Battle(props) {
    let battle = BattleMaps.get(props.battleID)
    const rows = battle.battleMap.length
    const cols = battle.battleMap[0].length
    const tileSize = 50    
    
    let [battleDataLoaded, setBattleDataLoaded] = React.useState(false)// 
    let [battleFinished, setBattleFinished] = React.useState(false)
    let [units, setUnits] = React.useState([])
    let [displayedUnit, setDisplayedUnit] = React.useState(battle.allies[0])// -   
    let [queue, setQueue] = React.useState([])// -,    
    let [isPlayersTurn, setIsPlayersTurn] = React.useState(true)//  ?
    let [curUnit, setCurUnit] = React.useState(null)// -  ?
    let [curUnitIndex, setCurUnitIndex] = React.useState(0)//  -   -
    let [currentTurn, setCurrentTurn] = React.useState(0)
    let [bmap, setBmap] = React.useState(() => {//   ,      
        //type: terrain / obstacle
        //subtype (): , , ,  ..
        //unitID: null,      - / id -,   
        //surface (): , ,  ..
        //trap: null / trap
        let initValue = {type: null, subtype: null, moveCost: null, unitID: null, surface: null, trap: null}
        return createMatrix(rows, cols, initValue)
    })
    let [focusedUnit, setFocusedUnit] = React.useState(null)// 
    let [curUnitJustDied, setCurUnitJustDied] = React.useState(false)

    // 
    let [battleMode, setBattleMode] = React.useState(null)//  (BATTLE, SPELL_BOOK, MENU etc)
    let [availableSpells, setAvailableSpells] = React.useState({})//   ( )
    let [activeSpells, setActiveSpells] = React.useState([])//    
    let [isCastingSpell, setIsCastingSpell] = React.useState(false)//     
    //     (,     2- :
    //1 -   , 2 -   )
    let [justCastedSpell, setJustCastedSpell] = React.useState(false)
    let [chosenSpell, setChosenSpell] = React.useState(null) 
    //    
    let [aoeCells, setAoeCells] = React.useState(() => {
        let initValue = {tint: NO_TINT}
        return createMatrix(rows, cols, initValue)
    })   
    let [visibleCells, setVisibleCells] = React.useState(() => {
        let initValue = {tint: NO_TINT}
        return createMatrix(rows, cols, initValue)
    })  
    let [distance, setDistance] = React.useState(0)//     
    // ,     (     - castSpell)
    let [aoe, setAoe] = React.useState(null)
    let [dmgText, setDmgText] = React.useState([])

    // 
    React.useEffect(()=>{startBattle()}, [])
    //   
    React.useEffect(()=>{if (battleDataLoaded) {
        setCurrentTurn(1)
    }}, [battleDataLoaded])
    // 
    React.useEffect(()=>{if (battleFinished) console.log('')}, [battleFinished])
    //   -     -
    React.useEffect(()=>{
        if (battleDataLoaded) {
            setCurUnit(units[curUnit.unitID])
            setDisplayedUnit(units[displayedUnit.unitID])
        }
    }, [units])
    //   
    React.useEffect(()=>{isPlayersTurn ? startTurn() : AI_turn()}, [isPlayersTurn, curUnitIndex])
    //   - 
    React.useEffect(()=>{beforeNextUnitTurn()}, [curUnitIndex])
    //,   - 
    React.useEffect(()=>{if (curUnitJustDied) beforeNextUnitTurn()}, [curUnitJustDied])
    //     
    React.useEffect(()=>{
        if (battleDataLoaded && queue.length) {
            checkBattleFinish()//       -
            // -  
            setBmap(prevState => prevState.map(row => {
                return row.map(item => {
                    if (item.unitID !== null) {
                        return (!units[item.unitID].alive) ? {...item, unitID: null} : item
                    } else return item
                })
            }))
            units.forEach(unit => {
                if (unit !== null) {
                    //   -  
                    if (!unit.alive && CHECK_EFFECT.isSummon(unit)) 
                    //     -       
                    //setUnits(prevState => prevState.filter(item => item.unitID !== unit.unitID))
                    setUnits(prevState => prevState.map(item => {
                        return (item !== null && item.unitID === unit.unitID) ? null : item
                    }))
                    //   - ,   
                    else if (unit.alive && CHECK_EFFECT.isSummon(unit)) {
                        if (!units[unit.summonerID].alive) killUnit(unit.unitID, setQueue, setUnits)
                    }
                }
            })
            //    -  
            if (!units[curUnit.unitID].alive) finishTurn("death")
        }
    }, [queue])

    // 

    React.useEffect(()=>{
        if (!isCastingSpell) {
            resetTint()
            setChosenSpell(null)
        }
    }, [isCastingSpell])
    // -  !!!   2     !!!
    React.useEffect(()=>{
        // ,      -  -    
        if (chosenSpell !== null && !curUnitJustDied) {
            setIsCastingSpell(true)
            resetTint()
            resetSpellRange()
            if (checkSpellAvailability(chosenSpell)) setSpellRange()
            else setIsCastingSpell(false)
        } else {
            resetSpellRange()
        }
    }, [chosenSpell])

    //   ,    
    React.useEffect(()=>{
        if (justCastedSpell) {
            //  
            if (chosenSpell) setChosenSpell(prevState => {
                return availableSpells[prevState.name]
            })
            //    -
            setUnits(prevState => prevState.map(unit => {
                if (unit !== null && unit.unitID === curUnit.unitID) {
                    return {...unit, availableSpells: {...availableSpells}}
                } else return unit
            }))
            //   
            setActiveSpells(prevState => prevState.map(item => {
                if (item !== null) {
                    let newCastsAmount, newCdLeft
                    if (item.castsPerTurn || item.castsPerFight) newCastsAmount = availableSpells[item.name].castsAmount
                    if (item.cd) newCdLeft = availableSpells[item.name].cdLeft
                    return {...item, castsAmount: newCastsAmount, cdLeft: newCdLeft}
                } else return null
            }))
            setJustCastedSpell(false)
        } 
    }, [availableSpells, justCastedSpell])

    //,        -    
    //    ,     ()
    React.useEffect(()=>{

        function setText(spell, caster, targetUnit) {
            let {dmg, dmgType} = spell
            let {x, y} = targetUnit

            function getStat() {
                if (dmgType[1] === 'dmg') {
                    if (dmgType[0] === 'mana') return 'Mana'
                    else if (dmgType[0] === 'will') return 'WP'
                    else return 'HP'
                } 
                else if (dmgType[1] === 'heal') {
                    if (dmgType[2] === 'will') return 'WP'
                    else if (dmgType[2] === 'int') return 'HP'
                }
                else return null
            }

            function getColor() {
                if (dmgType[1] === 'dmg') {
                    if (dmgType[0] === 'mana') return DAMAGE_MANA_TEXT_COLOR
                    else if (dmgType[0] === 'will') return DAMAGE_WP_TEXT_COLOR
                    else return DAMAGE_HP_TEXT_COLOR
                } 
                else if (dmgType[1] === 'heal') {
                    if (dmgType[2] === 'will') return HEAL_WP_TEXT_COLOR
                    else if (dmgType[2] === 'int') return HEAL_HP_TEXT_COLOR
                    else return HEAL_MANA_TEXT_COLOR
                }
                else return null
            }

            if (checkAlignments(spell, targetUnit)) {
                let stat, value, color
                if (dmgType[1] === 'dmg') {
                    stat = getStat()
                    value = getActualDmg(spell, caster, targetUnit, stat)
                    value = (value[0] === value[1] || value[1] === null) ? value[0] : value.join('-')
                    color = getColor()    
                } else if (dmgType[1] === 'heal') {
                    stat = getStat()
                    value = getActualHeal(spell, caster, targetUnit, stat)
                    value = (value[0] === value[1] || value[1] === null) ? value[0] : value.join('-')
                    color = getColor()
                } 
                return {x, y, text: value, color}
            }
            else return null
        }

        //    /  -  
        if (aoe !== null && chosenSpell !== null && curUnit && chosenSpell.dmg !== null) {
            let spell = chosenSpell, caster = curUnit, newDmgText = []   
            if (spell.area[0] === 'solo') {
                let {i, j} = aoe
                if (bmap[i][j].unitID !== null) {
                    let targetUnit = units[bmap[i][j].unitID]
                    let newText = setText(spell, caster, targetUnit)
                    if (newText !== null) newDmgText.push(newText)
                }
            } else {
                let {startI, startJ, finishI, finishJ} = aoe
                for (let ii = startI; ii < finishI; ii++)
                    for (let jj = startJ; jj < finishJ; jj++) {
                        if (bmap[ii][jj].unitID !== null && aoeCells[ii][jj].tint === DARK_TINT) {
                            let targetUnit = units[bmap[ii][jj].unitID]
                            let newText = setText(spell, caster, targetUnit)
                            if (newText !== null) newDmgText.push(newText)
                        }
                    }
            }  
            setDmgText(newDmgText)
        }
        else setDmgText([])
    }, [aoe])

    function initBattle() {
        let newBmap = bmap.map(arr => arr.slice())
        let newQueue = [], alliesArr = [], enemiesArr = [], newUnits = []

        // 
        battle.battleMap.forEach((row, i) => {
            row.forEach((symbol, j) => {            
                switch (symbol) {
                    case '=': {
                        newBmap[i][j].type = 'obstacle'
                        newBmap[i][j].subtype = 'deep water'
                        break
                    }     
                    case 'w': {
                        newBmap[i][j].type = 'obstacle'
                        newBmap[i][j].subtype = 'spruce'
                        break
                    }              
                    case '.': {
                        newBmap[i][j].type = 'terrain'
                        newBmap[i][j].subtype = 'grass'
                        newBmap[i][j].moveCost = 10
                        break
                    }
                    case 's': {
                        newBmap[i][j].type = 'terrain'
                        newBmap[i][j].subtype = 'sand'
                        newBmap[i][j].moveCost = 20
                        break
                    }   
                    case '~': {
                        newBmap[i][j].type = 'terrain'
                        newBmap[i][j].subtype = 'shallow water'
                        newBmap[i][j].moveCost = 30
                        break
                    }                
                    default: {}
                }
            })
        })
        
        // 
        battle.allies.forEach((item, index) => {
            item.alive = true//  / 
            item.bonusAP = 0//     -   
            item.bonusMP = 0//
            newBmap[item.y][item.x].unitID = item.unitID
            alliesArr.push(item)
            newUnits.push(item)
        })
        battle.enemies.forEach(item => {
            item.alive = true
            item.bonusAP = 0
            item.bonusMP = 0
            newBmap[item.y][item.x].unitID = item.unitID
            enemiesArr.push(item)
            newUnits.push(item)
        })

        setUnits(newUnits)
        setBmap(newBmap)

        alliesArr.sort((a, b) => a.IP - b.IP)
        enemiesArr.sort((a, b) => a.IP - b.IP)
        let lastAllyIndex = alliesArr.length - 1, lastEnemyIndex = enemiesArr.length - 1
        let flag = alliesArr[lastAllyIndex].IP > enemiesArr[lastEnemyIndex].IP ? 
                    'allyIsHandling' : 'enemyIsHandling'
        while (alliesArr.length || enemiesArr.length) {
            if (flag === 'enemyIsHandling' && alliesArr.length) {
                newQueue.push(enemiesArr.pop().unitID)
                flag = 'allyIsHandling'
            } else if (flag === 'allyIsHandling' && enemiesArr.length) {
                newQueue.push(alliesArr.pop().unitID)
                flag = 'enemyIsHandling'
            } else {
                while (alliesArr.length) newQueue.push(alliesArr.pop().unitID)
                while (enemiesArr.length) newQueue.push(enemiesArr.pop().unitID)
            }
        }

        let firstUnit = newUnits[newQueue[0]]
        setQueue(newQueue)
        setCurUnit(firstUnit)
        setDisplayedUnit(firstUnit)
        firstUnit.alignment === 'ally' ? setIsPlayersTurn(true) : setIsPlayersTurn(false)
    }

    function startBattle() {
        initBattle()
        setBattleMode('BATTLE')
        // ,          
        setBattleDataLoaded(true)
    }

    //   /  ( ,       )
    function checkAlignments(spell, targetUnit) {
        if (spell.target === 'enemy' && targetUnit.alignment === 'ally') return false
        else if (spell.target === 'ally' && targetUnit.alignment === 'enemy') return false
        else return true
    }

    //   
    function resetTint() {
        setAoeCells(prevState => {
            return  prevState.map(row => 
                row.map(item => {
                return {...item, tint: NO_TINT}
            }))
        }) 
        setAoe(null)
    }

    function checkBattleFinish() {
        let enemyFlag = false, allyFlag = false
        queue.forEach(index => {
            if (units[index].alignment === 'ally') allyFlag = true
            else if (units[index].alignment === 'enemy') enemyFlag = true
        })
        if (!enemyFlag || !allyFlag) setBattleFinished(true)
        return
    }

    // ,  - 
    function tintLine(line, tintType) {
        setVisibleCells(prevState => {
            let newState = prevState.map(line => line.slice())
            let i, j
            for (let k = 0; k < line.length; k++) {
                i = line[k].i
                j = line[k].j
                newState[i][j].tint = tintType
            }  
            return newState
        })
    }

    //  
    function tintPath(path) {
        setAoeCells(prevState => {
            let newState = prevState.map(path => path.slice())
            let i, j, currentDistance//       
            for (let k = 0; k < path.length; k++) {
                i = path[k].i
                j = path[k].j
                currentDistance = path[k].distance / 10
                newState[i][j].tint = (curUnit.MP >= currentDistance) ? BRIGHT_TINT : SHADOW_TINT
            }  
            return newState
        })                         
    }

    //AOV - -  - 
    function tintArea(targetPos, type, radius, tintType, AOV) {
        let {i, j} = targetPos
        if (AOV === true) setVisibleCells(prevState => {
            let newState = prevState.map(path => path.slice())
            let {startI, startJ, finishI, finishJ} = countArea(i, j, radius, rows, cols)
            if (type === 'rhombus') {
                if (chosenSpell.LOS) {                
                    for (let ii = startI; ii < finishI; ii++)
                        for (let jj = startJ; jj < finishJ; jj++) 
                            if (Math.abs(i - ii) + Math.abs(j - jj) <= radius) {
                                let LOS = drawLine(j, i, jj, ii, bmap)
                                tintLine(LOS, tintType)
                            }
                }
                else {
                    for (let ii = startI; ii < finishI; ii++)
                        for (let jj = startJ; jj < finishJ; jj++) 
                            if (Math.abs(i - ii) + Math.abs(j - jj) <= radius) {
                                newState[ii][jj].tint = tintType
                            }
                }
            }
            else if (type === 'linear') {
                if (chosenSpell.LOS) {                
                    for (let ii = startI; ii < finishI; ii++)
                        for (let jj = startJ; jj < finishJ; jj++) 
                            if (ii === i || jj === j) {
                                let LOS = drawLine(j, i, jj, ii, bmap)
                                tintLine(LOS, tintType)
                            }
                }
                else {
                    for (let ii = startI; ii < finishI; ii++)
                        for (let jj = startJ; jj < finishJ; jj++) 
                            if (ii === i || jj === j) {
                                newState[ii][jj].tint = tintType
                            }
                }
            }
            return newState
        })
        // 
        else setAoeCells(prevState => {
            let newState = prevState.map(path => path.slice())
            let {startI, startJ, finishI, finishJ} = countArea(i, j, radius, rows, cols)

            if (type === 'solo') {
                newState[i][j].tint = tintType
                setAoe({i, j})
            }
            else if (type === 'square') {
                for (let ii = startI; ii < finishI; ii++)
                    for (let jj = startJ; jj < finishJ; jj++) newState[ii][jj].tint = tintType
                setAoe({startI, startJ, finishI, finishJ})
            }
            else if (type === 'rhombus') {
                for (let ii = startI; ii < finishI; ii++)
                    for (let jj = startJ; jj < finishJ; jj++) 
                        if (Math.abs(i - ii) + Math.abs(j - jj) <= radius) {
                            newState[ii][jj].tint = tintType
                        }
                setAoe({startI, startJ, finishI, finishJ})
            }
            else if (type === 'cross') {
                for (let ii = startI; ii < finishI; ii++)
                    for (let jj = startJ; jj < finishJ; jj++) 
                        if (ii === i || jj === j) {
                            newState[ii][jj].tint = tintType
                        }
                setAoe({startI, startJ, finishI, finishJ})
            }
            else if (type === 'ring') {
                let diameter = 2 * radius + 1
                for (let ii = startI; ii < finishI; ii++)
                    for (let jj = startJ; jj < finishJ; jj++) 
                        if (ii === startI || ii === startI + diameter - 1 || jj === startJ || jj === startJ + diameter - 1) {
                            newState[ii][jj].tint = tintType
                        }
                setAoe({startI, startJ, finishI, finishJ})
            }
            else if (type === 'allMap') {
                for (let ii = startI; ii < finishI; ii++)
                    for (let jj = startJ; jj < finishJ; jj++) 
                        newState[ii][jj].tint = tintType
                setAoe({startI, startJ, finishI, finishJ})
            }
            return newState
        })
    }

    // -  
    function setSpellRange() {
        let areaType = chosenSpell.range[2] ? chosenSpell.range[2] : 'rhombus'//   ,    
        tintArea({i: curUnit.y, j: curUnit.x}, areaType, chosenSpell.range[1], SHADOW_TINT, true)
        if (chosenSpell.range[0]) tintArea({i: curUnit.y, j: curUnit.x}, areaType, chosenSpell.range[0] - 1, NO_TINT, true)
    }

    // - 
    function resetSpellRange() {
        setVisibleCells(prevState => {
            return  prevState.map(row => 
                row.map(item => {
                return {...item, tint: NO_TINT}
            }))
        })
    }

    function checkSpellAvailability(spell) {
        if (checkState('Silenced', curUnit)) return false
        if (curUnit.AP < spell.apcost) return false
        else if (curUnit.Mana < spell.manacost) return false
        else if (spell.cd && spell.cdLeft) return false
        else if (spell.castsPerTurn && spell.castsAmount === spell.castsPerTurn) return false
        else if (spell.castsPerFight && spell.castsAmount === spell.castsPerFight) return false
        else return true
    }

    //    
    function affectUnit(caster, spell, targetPos, targetUnit, units, setUnits, setQueue, bmap, setBmap) {
        //      
        if (targetUnit !== null) {
            if (CHECK_EFFECT.hasSpellInvul(targetUnit)) return
        }

        spell.spellEffect(caster, spell, targetPos, targetUnit, units, setUnits, setQueue, bmap, setBmap)
    }

    //    
    function affectSurface(caster, spell, targetPos, units, setUnits, setQueue, bmap, setBmap) {
        spell.spellEffect(caster, spell, targetPos, null, units, setUnits, setQueue, bmap, setBmap, true)
    }

    // 
    function castSpell(caster, targetPos, spell) {
        //      (     )
        if(!checkSpellAvailability(chosenSpell)) return

        let newCastsAmount = spell.castsAmount, newCdLeft = spell.cdLeft
        //        -  1 
        if (spell.castsPerTurn || spell.castsPerFight) newCastsAmount += 1
        if (spell.cd) newCdLeft = spell.cd// 
        setAvailableSpells(prevState => {
            return {...prevState, [spell.name]: {...prevState[spell.name], castsAmount: newCastsAmount, cdLeft: newCdLeft}}
        })
        setJustCastedSpell(true)

        let {i, j} = targetPos
        modifyUnitStats(caster, 
            ['AP', 'Mana'], 
            [-spell.apcost, -spell.manacost],
            setUnits)

        if (spell.target === 'notarget') {
            if (spell.spellEffect !== null) {
                affectUnit(caster, spell, targetPos, null, units, setUnits, setQueue, bmap, setBmap)
            }
        }
        else if (spell.area[0] === 'solo') {
            if (bmap[i][j].type === 'terrain' && spell.affectsSurface) affectSurface(caster, spell, {i, j}, units, setUnits, setQueue, bmap, setBmap)
            if (bmap[i][j].unitID !== null) {
                let targetUnit = units[bmap[i][j].unitID]
                if (checkAlignments(spell, targetUnit)) affectUnit(caster, spell, targetPos, targetUnit, units, setUnits, setQueue, bmap, setBmap)
            }
        }
        else {
            let {startI, startJ, finishI, finishJ} = aoe
            for (let ii = startI; ii < finishI; ii++)
                for (let jj = startJ; jj < finishJ; jj++) {
                    if (aoeCells[ii][jj].tint === DARK_TINT) {
                        if (bmap[ii][jj].type === 'terrain' && spell.affectsSurface) affectSurface(caster, spell, {i: ii, j: jj}, units, setUnits, setQueue, bmap, setBmap)
                        if (bmap[ii][jj].unitID !== null) {
                            let targetUnit = units[bmap[ii][jj].unitID]
                            if (checkAlignments(spell, targetUnit)) affectUnit(caster, spell, targetPos, targetUnit, units, setUnits, setQueue, bmap, setBmap)
                        }
                    }
                }
        }
    }

    function handleObstacleOver(e) {
        if (isPlayersTurn) resetTint()
    }

    function handleMouseOver(e) {    
        if (isPlayersTurn) {
            resetTint()    
            let {x, y} = e.target.gamecoords
            let bmapCell = bmap[y][x]
            if (bmapCell.unitID !== null) setDisplayedUnit(units[bmapCell.unitID])
            if (isPlayersTurn) {
                if (!isCastingSpell) {
                    if (bmapCell.type === 'terrain' && bmapCell.unitID === null) {                    
                        let start = {i: curUnit.y, j: curUnit.x}
                        let finish = {i: y, j: x}
                        let pathfindingMode = "DEFAULT"
                        if (CHECK_EFFECT.dontHaveMovementPenalty(curUnit)) pathfindingMode = "NO_PENALTY"
                        let path = findPath(start, finish, bmap, pathfindingMode)
                        setDistance(path[0].distance / 10)
                        tintPath(path)
                    }
                }
                else {
                    if (bmapCell.type === 'terrain' && visibleCells[y][x].tint === SHADOW_TINT) tintArea({i: y, j: x}, chosenSpell.area[0], chosenSpell.area[1], DARK_TINT, false)
                }
            }
        }
    }

    function handleMouseOut(e) {
        if (isPlayersTurn) setDisplayedUnit(curUnit)
    }

    function handleMouseDown(e) {
        if (isPlayersTurn) {
            let {x, y} = e.target.gamecoords
            let bmapCell = bmap[y][x]
            if (isPlayersTurn) {
                if (!isCastingSpell) {
                    if (bmapCell.unitID === null && curUnit.MP >= distance) {
                        setUnitStats(curUnit, 
                                        ['MP', 'x', 'y'], 
                                        [curUnit.MP - distance, x, y],
                                        bmap, setBmap, setUnits)
                        resetTint()
                    }
                }   
                else {
                    if (bmapCell.type === 'terrain' && visibleCells[y][x].tint === SHADOW_TINT) {
                        castSpell(curUnit, {i: y, j: x}, chosenSpell)
                        //tintLine(drawLine(j, i, jj, ii, bmap), BRIGHT_TINT)// 
                    }
                }        
            }  
        }    
    }

    function setNextCurUnitIndex() {
        if (curUnitIndex + 1 >= queue.length) {
            setCurUnitIndex(0)
            setCurrentTurn(prevState => prevState + 1)
        }
        else setCurUnitIndex(prevState => prevState + 1)
    }

    //     -
    function beforeNextUnitTurn() {
        if (battleDataLoaded) {
            resetTint()
            setIsCastingSpell(false)
            let newCurUnit = units[queue[curUnitIndex]]
            setCurUnit(newCurUnit)
            setDisplayedUnit(newCurUnit)
            if (newCurUnit.alignment === 'ally') {
                setIsPlayersTurn(true)
                setAvailableSpells({...newCurUnit.availableSpells})
                setActiveSpells([...newCurUnit.activeSpells])
            }
            else setIsPlayersTurn(false)
        }
    }

    function AI_turn() {
        let curUnit = units[queue[curUnitIndex]]
        if (!curUnit || curUnit.alignment !== 'enemy') return
        setTimeout(() => {
            setNextCurUnitIndex()
        }, 100)//  - 500
        startTurn()
    }

    function startTurn() {
        if (queue.length) {
            setCurUnitJustDied(false)
            let curUnit = units[queue[curUnitIndex]]

            //   - ,    
            if (CHECK_EFFECT.skipsTurn(curUnit)) {
                setTimeout(() => finishTurn(), 100)
            }

            // -   
            if (curUnit.conditions.length) {
                curUnit.conditions.forEach(cond => {
                    if (cond.delayedEffect !== null) {
                        cond.delayedEffect(cond.source, cond.spell, {i: curUnit.y, j: curUnit.x}, curUnit, units, setUnits, setQueue, bmap, setBmap)
                        if (cond.duration - 1 !== 0) decreaseConditionDuration(cond, curUnit, setUnits)
                        else removeCondition(cond, curUnit, setUnits)
                    }
                })
            }
            // -    (  ..) +  - null  
            if (curUnit.states.length) {
                curUnit.states.forEach(st => {
                    if (st.delayedEffect !== null || st.source === null) { 
                        // 
                        if (st.delayedEffect !== null) {
                            let delEf = st.delayedEffect
                            delEf.func(st.source, delEf.dmg, delEf.dmgType, curUnit, setUnits, setQueue)
                        }
                        //    -   / 
                        if (st.duration !== "permanent") {
                            if (st.duration - 1 !== 0) decreaseStateDuration(st, curUnit, setUnits)
                            else removeState(st, curUnit, setUnits)
                        }
                    }
                })
            }
            
            //   -  ,      / 
            for (let i = 0; i < units.length; i++) {
                if (units[i] === null) continue
                // 
                if (units[i].conditions.length) {
                    units[i].conditions.forEach((cond, index) => {
                        if (cond.source.unitID === curUnit.unitID && cond.delayedEffect === null) {
                            if (cond.duration - 1 !== 0) decreaseConditionDuration(cond, units[i], setUnits)
                            else removeCondition(cond, units[i], setUnits, "REMOVE_BY_DURATION")
                        }
                    })
                }
                // 
                if (units[i].states.length) {
                    units[i].states.forEach((st, index) => {
                        if (st.duration !== "permanent" && st.source !== null && st.source.unitID === curUnit.unitID && st.delayedEffect === null) {
                            if (st.duration - 1 !== 0) decreaseStateDuration(st, units[i], setUnits)
                            else {
                                if (st.name === 'Life time') killUnit(units[i].unitID, setQueue, setUnits)
                                else removeState(st, units[i], setUnits)
                            }
                        }
                    })
                }
            }

            //   (, -     ...)
            if (curUnit.alignment === 'ally' && Object.keys(availableSpells).length) {
                setAvailableSpells(prevState => {
                    let newState = {...prevState}
                    Object.keys(prevState).forEach(spName => {
                        let spell = prevState[spName]
                        if (spell.castsPerTurn) newState[spName].castsAmount = 0
                        if (spell.cdLeft) newState[spName].cdLeft = spell.cdLeft - 1
                    })
                    return newState
                })
                setActiveSpells(prevState => prevState.map(spell => {
                    if (spell !== null) {
                        let newCastsAmount, newCdLeft
                        if (spell.castsPerTurn) newCastsAmount = 0
                        if (spell.cd) newCdLeft = (spell.cdLeft > 0) ? spell.cdLeft - 1 : 0
                        return {...spell, castsAmount: newCastsAmount, cdLeft: newCdLeft}
                    } else return null
                }))
            }
        }
    }

    function finishTurn(reason = null) {
        if (!isPlayersTurn) return
        //    
        if (reason !== "death") {
            //   
            setUnitStats(curUnit, 
                ['AP', 'MP'], 
                [curUnit.maxAP + curUnit.bonusAP, curUnit.maxMP + curUnit.bonusMP],
                bmap, setBmap, setUnits)
        }  

        setIsPlayersTurn(false)
        setNextCurUnitIndex()

        //      -
        setUnits(prevState => prevState.map(unit => {
            if (unit !== null && unit.unitID === curUnit.unitID) {
                return {...unit, availableSpells: {...availableSpells}, activeSpells: [...activeSpells]}
            } else return unit
        }))
        //    
        if (reason === "death") setCurUnitJustDied(true)
    }

    // 
    function openSpellBook() {
        if (battleMode === 'BATTLE') {
            setBattleMode('SPELL_BOOK')
            return
        } else {
            setBattleMode('BATTLE')
            return
        }
    }

    function hoverQueueImg(id) {
        if (isPlayersTurn) {
            let unit = units[id]
            setDisplayedUnit(unit)
            setFocusedUnit({x: unit.x, y: unit.y})
        } 
    }

    function removeFocus() {
        //setDisplayedUnit(curUnit)
        setFocusedUnit(null)
    }

    //  .    
    let obstacles = []
    let terrains = []
    let surfaces = []
    let allies = []
    let enemies = []
    let bmapText = []

    let keyID = 0//  
    bmap.forEach((row, i) => {
        row.forEach((item, j) => {            
            let x = tileSize * j;
            let y = tileSize * i;
            let gamecoords = {x: j, y: i}
            let subtype = item.subtype
            let surface = item.surface
            if (item.type === 'obstacle') {
                obstacles.push(<Sprite key={keyID++} width={tileSize} height={tileSize} x={x} y={y} image={obstaclesObj[subtype]} gamecoords={gamecoords} interactive={true}
                                pointerover={handleObstacleOver}/>)
            }
            if (item.type === 'terrain') {
                let tintVal
                //    
                if (focusedUnit !== null && focusedUnit.x === j && focusedUnit.y === i) tintVal = FOCUS_TINT
                // ,     - 
                else (aoeCells[i][j].tint === NO_TINT) ? tintVal = visibleCells[i][j].tint : tintVal = aoeCells[i][j].tint
                terrains.push(<Sprite key={keyID++} width={tileSize} height={tileSize} x={x} y={y} image={terrainObj[subtype]} gamecoords={gamecoords} interactive={true} 
                                tint={tintVal}
                                pointerover={handleMouseOver}
                                pointerout={handleMouseOut}
                                pointerdown={handleMouseDown}/>)
            }
            if (item.surface !== null) {
                surfaces.push(<Sprite key={keyID++} width={tileSize} height={tileSize} x={x} y={y} image={surfacesObj[surface]}/>)
            }
            if (item.unitID !== null) {
                let unit = units[item.unitID]
                // 
                if (typeof unit === 'undefined') {
                    console.log(row, item, units)
                }
                // 
                if (unit.alignment === 'ally') 
                    allies.push(<Sprite key={keyID++} width={tileSize} height={tileSize} x={x} y={y} unitInfo={unit} image={unit.img}/>)
                else if (unit.alignment === 'enemy')
                    enemies.push(<Sprite key={keyID++} width={tileSize} height={tileSize} x={x} y={y} unitInfo={unit} image={unit.img}/>)
            }
        })
    })

    dmgText.forEach((item, index) => {
        bmapText.push(<Text key={index}
                            text={item.text}
                            anchor={0.5}
                            x={item.x * 50 + 25}
                            y={item.y * 50 - 15}
                            style={
                            new PIXI.TextStyle({
                                fontFamily: 'sans-serif',
                                fontSize: 16,
                                fill: item.color,
                                stroke: 'rgb(0, 0, 0)',
                                strokeThickness: 5,
                                lineJoin: "bevel",
                                wordWrap: false,
                            })
                            }
                        />)
    })

    return(
        <>
        {(battleDataLoaded && !battleFinished) ? 
            <div className="battle">
                {battleMode === 'SPELL_BOOK' && <SpellBook isPlayersTurn={isPlayersTurn}
                                                           curUnit={curUnit}
                                                           availableSpells={availableSpells}
                                                           activeSpells={activeSpells}
                                                           setActiveSpells={setActiveSpells}/>}                 
                <BattleField battleObjects={{obstacles, terrains, surfaces, allies, enemies, bmapText}}
                             isPlayersTurn={isPlayersTurn}
                             battleMode={battleMode}
                             curUnit={curUnit}
                             availableSpells={availableSpells}
                             activeSpells={activeSpells}
                             setActiveSpells={setActiveSpells}
                             checkSpellAvailability={checkSpellAvailability}
                             setChosenSpell={setChosenSpell}/>
                <BattleMenu isPlayersTurn={isPlayersTurn}
                            displayedUnit={displayedUnit || curUnit}
                            units={units}
                            queue={queue}
                            currentTurn={currentTurn}
                            finishTurn={finishTurn}
                            openSpellBook={openSpellBook}
                            setIsCastingSpell={setIsCastingSpell}
                            hoverQueueImg={hoverQueueImg}
                            removeFocus={removeFocus}/>
            </div> :
            <div className="GG">GG</div>
        }  
        </>    
    )
}