import React from "react"
import '../styles/styles.css'
import { getPrimaryValue } from './functions/battleFunctions'

import img_emptySpellSlot from './game resources/img/spells/empty spell slot.png';


export default function SkillBar(props) {   
    let [currentSkill, setCurrentSkill] = React.useState(null)
    let [currentIndex, setCurrentIndex] = React.useState(null)
    let skillBarItems = []
    let activeSpells = props.activeSpells
    let setActiveSpells = props.setActiveSpells

    //-    
    function checkCurrentSpellAvailability(id) {
        if (!activeSpells[id]) return false
        if (props.isPlayersTurn) {
            if (props.checkSpellAvailability(activeSpells[id])) return true
        }
    }

    function dragStartHandler(e, skill) {
        if (props.battleMode !== 'SPELL_BOOK') return
        setCurrentSkill(skill)
        setCurrentIndex(Number(e.target.id))
    }

    function dragOverHandler(e) {
        if (props.battleMode !== 'SPELL_BOOK') return
        e.preventDefault()
    }

    function dropHandler(e) {
        if (props.battleMode !== 'SPELL_BOOK') return
        e.preventDefault()
        let id = Number(e.target.id)
        setActiveSpells(prevState => prevState.map((item, index) => {
            if (index === id) return currentSkill
            if (index === currentIndex) return (activeSpells[id] !== null) ? activeSpells[id] : null
            return item
        }))
    }
    
    let src, spellInfo, isAvailable
    activeSpells.forEach((skill, i) => {
        if (skill !== null) {
            src = skill.img
            spellInfo = skill
        } else {
            src = img_emptySpellSlot
            spellInfo = null
        }
        isAvailable = checkCurrentSpellAvailability(i)        
        skillBarItems.push(
            <SkillBarItem key={i} id={i} src={src}
                            isPlayersTurn={props.isPlayersTurn}
                            battleMode={props.battleMode}
                            curUnit={props.curUnit}
                            activeSpells={props.activeSpells}
                            setActiveSpells={props.setActiveSpells}
                            availableSpells={props.availableSpells}
                            checkSpellAvailability={props.checkSpellAvailability}
                            setChosenSpell={props.setChosenSpell}
                            spellInfo={spellInfo}
                            isAvailable={isAvailable}
                            draggable={true}
                            dragStartHandler={dragStartHandler}
                            dragOverHandler={dragOverHandler}
                            dropHandler={dropHandler}/> 
            )
    }) 

    return(
        <div className="skill-bar">
            {skillBarItems}
        </div>
    )
}

function SkillBarItem(props) {
    let [isHovered, setIsHovered] = React.useState(false)
    let activeSpells = props.activeSpells
    let isAvailable = props.isAvailable
    let itemStyle = (isAvailable) ? "skill--item position-relative" : "skill--item not-available position-relative"

    function chooseSpell(e) {
        setIsHovered(false)
        if (props.battleMode !== 'BATTLE' || e.nativeEvent.button === 2) return
        if (isAvailable) props.setChosenSpell(activeSpells[props.id])
    }

    function removeActiveSpell(e) {
        e.preventDefault()
        if (props.battleMode !== 'SPELL_BOOK') return
        let id = Number(e.target.id)
        props.setActiveSpells(prevState => prevState.map((item, index) => {
            if (index === id) return null
            return item
        }))
    }

    function showSpellInfo() {
        if (activeSpells[props.id]) setIsHovered(true)//     
    } 

    return (
        <div className={itemStyle} id={props.id}>
            {(isHovered && props.isPlayersTurn) && <SkillBarTooltip spellInfo={props.spellInfo} curUnit={props.curUnit}/>}
            <img src={props.src} alt=""
                 id={props.id}
                 onMouseOver={showSpellInfo}
                 onMouseOut={()=>setIsHovered(false)}
                 onMouseDown={e=>chooseSpell(e)}
                 onContextMenu={e=>removeActiveSpell(e)}
                 onDragStart={e => props.dragStartHandler(e, props.spellInfo)}
                 onDragOver={e => props.dragOverHandler(e)}
                 onDrop={e => props.dropHandler(e)}/>
        </div>
    )
}

function SkillBarTooltip(props) {
    let {name, runame, description, img, cd, dmg, dmgPerTurn, dmgType, duration, manacost, apcost, range, area, 
        target, LOS, castsPerTurn, castsPerFight, requirment} = props.spellInfo
       
    let cdLeft, castsAmount, dmgSpan = <></>
    if (cd !== 0) cdLeft = props.spellInfo.cdLeft
    if (castsPerTurn !== null || castsPerFight !== null) castsAmount = props.spellInfo.castsAmount

    let actDmg, minDmg, maxDmg
    if (dmg !== null) {
        actDmg = getPrimaryValue(dmg, dmgType, props.curUnit)
        minDmg = actDmg[0]
        maxDmg = actDmg[1]
    }
    let actDmgPerTurn, minDmgPerTurn, maxDmgPerTurn
    if (dmgPerTurn !== null) {
        actDmgPerTurn = getPrimaryValue(dmgPerTurn, dmgType, props.curUnit)
        minDmgPerTurn = actDmgPerTurn[0]
        maxDmgPerTurn = actDmgPerTurn[1]
    }
    //   (,   )
    let range2span = range[2] ? (<span>, {areaTrans[range[2]]}</span>) : <></>
    if (dmg !== null) dmgSpan = (maxDmg !== null) ? (<span>{minDmg}-{maxDmg}</span>) : (<span>{minDmg}</span>)

    //     
    return(
        <div className="skill--tooltip skill-bar-pos">
            <div className="tooltip--content">
                <div className="tooltip--header">
                    <p className="tooltip--header-name">{runame}</p>
                    <img className="tooltip--header-image" src={img} alt=""/>
                </div>
                {(dmg !== null) && 
                ((dmgType[1] === 'dmg') ? 
                <p>: {dmgSpan}</p> : 
                <p>: {dmgSpan}</p>)}
                {(dmgPerTurn !== null) && <p> : {minDmgPerTurn}-{maxDmgPerTurn}</p>}
                {(dmgType !== null && dmgType[1] !== 'heal') && <p> : {dmgTypeTrans[dmgType[0]]}</p>}
                {(duration !== null) && <p>: {duration}</p>}
                <p>: {manacost}</p>
                <p>: {apcost}</p>
                {(cd !== 0) && <p>: {cdLeft}/{cd}</p>}
                {(castsPerTurn !== null) && <p>  : {castsAmount}/{castsPerTurn}</p>}
                {(range[1] !== 0) ? <p>: {range[0]}-{range[1]}{range2span}</p> : <p>: 0</p>}
                <p>: {areaTrans[area[0]]}</p>     
                {(area[1] && area[1] !== 99) && <p> : {area[1]}</p>}
                <p>: {targetTrans[target]}</p>
                <p> : {LOS ? '' : ''}</p>
                {(castsPerFight !== null) && <p>  : {castsAmount}/{castsPerFight}</p>}
                {(requirment !== null) && <p>: {requirment}</p>}
                <p className="tooltip--description">{description}</p>
            </div>
        </div>
    )
}


//
let dmgTypeTrans = {
    'phys': '',
    'fire': '',
    'water': '',
    'air': '',
    'earth': '',
    'light': '',
    'dark': '',
    'will': '',
    'poison': '',
    'pure': '',
}

let targetTrans = {
    'any': '',
    'enemy': '',
    'ally': '',
    'notarget': ''
}

let areaTrans = {
    'solo': ' ',
    'allMap': ' ',
    'square': '',
    'cross': '',
    'ring': '',
    'rhombus': '',
    'linear': ' ',
}