import React from "react"
import '../styles/styles.css'
import '../styles/terrain.css'

import * as PIXI from "pixi.js";
import { Stage, Container, Sprite } from '@pixi/react';
import SkillBar from './SkillBar'


export default function BattleField(props) {
    let {obstacles, terrains, surfaces, allies, enemies, bmapText} = props.battleObjects;

    return(
        <div className="battle--field">
            <Stage width={750} height={500}>
                <Container>
                    {obstacles}
                    {terrains}
                    {surfaces}
                    {allies}
                    {enemies}
                    {bmapText}
                </Container>
            </Stage>
            <SkillBar isPlayersTurn={props.isPlayersTurn}
                      battleMode={props.battleMode}
                      curUnit={props.curUnit}
                      availableSpells={props.availableSpells}
                      activeSpells={props.activeSpells} 
                      setActiveSpells={props.setActiveSpells}
                      checkSpellAvailability={props.checkSpellAvailability}
                      setChosenSpell={props.setChosenSpell}/>
        </div>
    )
}