import React from "react"
import statusMap from './game resources/statuses'
import { getPrimaryValue } from './functions/battleFunctions'



export default function UnitEffects(props) {   
    let curUnit = props.displayedUnit
    let statusItems = [], spellConditionItems = [], stateItems = [], statuses = [], conditions = [], states = []

    //   
    if (curUnit.statuses.length) {
        statuses = curUnit.statuses
        statusItems = statuses.map((item, index) => {
            let status = statusMap.get(item)
            return <UnitEffectsItem key={index} item={status} img={status.img} mode={'status'} positiveness={"neutral"}/>
        })
    }

    //   
    if (curUnit.conditions.length) {
        conditions = curUnit.conditions
        spellConditionItems = conditions.map((cond, index) => {
            return <UnitEffectsItem key={index} item={cond} img={cond.spell.img} mode={'condition'} positiveness={cond.positiveness}/>
        })
    }
    //   
    if (curUnit.states.length) {
        states = curUnit.states
        stateItems = states.map((st, index) => {
            return <UnitEffectsItem key={index} item={st} img={st.img} mode={'state'} positiveness={st.positiveness}/>
        })
    }

    let statusesSpan = (curUnit.statuses.length) ? <div className="unit-effect-header">:</div> : <></>
    let conditionsSpan = (curUnit.conditions.length) ? <div className="unit-effect-header"> :</div> : <></>
    let statesSpan = (curUnit.states.length) ? <div className="unit-effect-header">:</div> : <></>

    return(
        <>
        {statusesSpan}
        <div className="unit-effects">{statusItems}</div>
        {conditionsSpan}
        <div className="unit-effects">{spellConditionItems}</div>
        {statesSpan}
        <div className="unit-effects">{stateItems}</div>
        </> 
    )
}

function UnitEffectsItem(props) {
    let [isHovered, setIsHovered] = React.useState(false)

    let style = "unit-effects--item", positivenessStyle
    if (props.positiveness === "neutral") positivenessStyle = " neutral-effect-icon"
    else if (props.positiveness === "negative") positivenessStyle = " negative-effect-icon"
    else if (props.positiveness === "positive") positivenessStyle = " positive-effect-icon"
    style += positivenessStyle

    return (
        <div className={style}>
            {isHovered && props.mode === 'status' && <StatusTooltip status={props.item}/>}
            {isHovered && props.mode === 'condition' && <SpellConditionTooltip condition={props.item}/>}
            {isHovered && props.mode === 'state' && <StateTooltip state={props.item}/>}
            <img src={props.img} alt=""
                 onMouseOver={()=>setIsHovered(true)}
                 onMouseOut={()=>setIsHovered(false)}/>
        </div>
    )
}



function StatusTooltip(props) {
    let status = props.status
    let statusName = status.name
    let description = status.description
    let headerStyle = (description === null) ? "tooltip--header--status-no-desc" : "tooltip--header"

    return(
        <div className="unit-effects--tooltip">
            <div className="tooltip--content">
                <div className={headerStyle}>
                <p className="tooltip--header-name">{statusName}</p>
                </div>
                {(description !== null) && <p className="tooltip--description">{description}</p>}
            </div>
        </div>
    )
}

function SpellConditionTooltip(props) {
    let condition = props.condition
    let spellName = condition.spell.runame
    let source = condition.source.name
    let duration = condition.duration
    let dispellable = condition.dispellable
    let statsArr = condition.effect.statsArr
    let valuesArr = condition.effect.valuesArr

    let effects = []
    effects = statsArr.map((stat, index) => {
        if (stat !== 'bonusAP' && stat !== 'bonusMP') {
            let effectStyle = (valuesArr[index][0] === '+') ? "buff-effect-text" : "debuff-effect-text"
            return <span className={effectStyle} key={index}>{valuesArr[index]} {conditionTrans[stat]}</span>
        }
    })
    if (condition.effect.state) {
        let state = condition.effect.state
        let effectStyle = (state.isPositive) ? "buff-effect" : "debuff-effect"
        effects.push(<span className={effectStyle} key={-1}>{state.name}</span>)
    }
    
    return(
        <div className="unit-effects--tooltip">
            <div className="tooltip--content">
                <div className="tooltip--header">
                <p className="tooltip--header-name">{spellName}</p>
                </div>
                <p>: {source}</p>
                <p>: {duration}</p>
                <p> : {dispellable ? "" : ""}</p>
                {(effects.length > 0) && <p>: {effects}</p>}
            </div>
        </div>
    )
}


function StateTooltip(props) {
    let state = props.state
    let stateName = state.runame
    let description = state.description
    let type = state.type
    let dispellable = state.dispellable
    let duration = state.duration
    let statsArr = state.effect.statsArr
    let valuesArr = state.effect.valuesArr
    let delEf = state.delayedEffect

    let actDmgPerTurn, minDmgPerTurn, maxDmgPerTurn
    if (delEf.dmg !== null) {
        if (delEf.dmgRecalulates) {
            actDmgPerTurn = getPrimaryValue(delEf.dmg, delEf.dmgType, delEf.source)
            minDmgPerTurn = actDmgPerTurn[0]
            maxDmgPerTurn = actDmgPerTurn[1]
        }
        else {
            minDmgPerTurn = delEf.dmg[0]
            maxDmgPerTurn = delEf.dmg[1]
        }
    }
    

    let effects = statsArr.map((stat, index) => {
        if (stat !== 'bonusAP' && stat !== 'bonusMP') {
            let effectStyle = (valuesArr[index][0] === '+') ? "buff-effect-text" : "debuff-effect-text"
            return <span className={effectStyle} key={index}>{valuesArr[index]} {conditionTrans[stat]}</span>
        }
    })

    return(
        <div className="unit-effects--tooltip">
            <div className="tooltip--content">
                <div className="tooltip--header">
                <p className="tooltip--header-name">{stateName}</p>
                </div>
                {(type !== null) && <p>: {stateTypeTrans[type]}</p>}
                <p>: {duration === "permanent" ? "" : duration}</p>
                {(delEf !== null && delEf.dmg.length > 0) && <p>: {minDmgPerTurn}-{maxDmgPerTurn} ({dmgTypeTrans[delEf.dmgType[0]]})</p>}
                <p> : {dispellable ? "" : ""}</p>
                {(effects.length > 0) && <p>: {effects}</p>}
                {(description !== null) && <p className="tooltip--description">{description}</p>}
            </div>
        </div>
    )
}


let conditionTrans = {
    'HP': '',
    'Mana': '',
    'WP': '',
    'AP': '',
    'MP': '',

    'vit': '',
    'wis': '',
    'str': '',
    'agi': '',
    'int': '',
    'will': '',
    'AC': 'AC',
    'ini': '',

    'physRes': '   ',
    'airRes': '  ',
    'fireRes': '  ',
    'earthRes': '  ',
    'waterRes': '  ',
    'lightRes': '  ',
    'darkRes': '  ',
}

let stateTypeTrans = {
    'elemental': '',
    'mind': '',
    'body': '',
    'movement': '',
    'poison': ''
}

let dmgTypeTrans = {
    'phys': '',
    'fire': '',
    'water': '',
    'air': '',
    'earth': '',
    'light': '',
    'dark': '',
    'will': '',
    'poison': '',
    'pure': '',
}