import { createMatrix } from "./otherFunctions"
import { PriorityQueue } from "./PriorityQueue"

function manhattanDistance(i1, j1, i2, j2) {
    return (Math.abs(i2 - i1) + Math.abs(j2 - j1)) * 10
}

//       (ni, nj)
function processCell(ni, nj, i, j, finish, f, g, h, mc, parents, processed, occupied, neighbours, mode) {
    if (!occupied[ni][nj] && !processed[ni][nj]) {
        if (g[i][j] + mc[ni][nj] < g[ni][nj]) {
            //      
            if (mode === "DEFAULT") g[ni][nj] = g[i][j] + mc[ni][nj]
            else g[ni][nj] = g[i][j] + 10
            parents[ni][nj] = {i, j}
        }                 
        h[ni][nj] = manhattanDistance(ni, nj, finish.i, finish.j)
        f[ni][nj] = g[ni][nj] + h[ni][nj]
        neighbours.push({i: ni, j: nj, f: f[ni][nj]})
    } else return
}

// *
export function findPath(start, finish, map, mode = "DEFAULT") {
    let rows = map.length, cols = map[0].length//-   
    let INF = 888888//  
    let f = createMatrix(rows, cols, 0)//g + h
    let g = createMatrix(rows, cols, INF)//-    
    let h = createMatrix(rows, cols, 0)// -,    -  
    let mc = createMatrix(rows, cols, 0)//  [i][j]     
    let parents = createMatrix(rows, cols, null)//  [i][j] ,    
    let processed = createMatrix(rows, cols, false)// 
    let occupied = createMatrix(rows, cols, false)//  (  -)
    for (let i = 0; i < map.length; i++)
        for (let j = 0; j < map[0].length; j++) {
            if (map[i][j].type === 'obstacle' || map[i][j].unitID !== null) occupied[i][j] = true
            mc[i][j] = map[i][j].moveCost
        }
    mc[start.i][start.j] = 0//     
            
    let neighbours = new PriorityQueue()

    // 
    let i = start.i, j = start.j, nextCell
    h[i][j] = manhattanDistance(i, j, finish.i, finish.j)
    g[i][j] = 0
    processed[i][j] = true
    while (true) {
        if (i + 1 <= rows - 1) processCell(i + 1, j, i, j, finish, f, g, h, mc, parents, processed, occupied, neighbours, mode)
        if (j + 1 <= cols - 1) processCell(i, j + 1, i, j, finish, f, g, h, mc, parents, processed, occupied, neighbours, mode)
        if (i - 1 >= 0) processCell(i - 1, j, i, j, finish, f, g, h, mc, parents, processed, occupied, neighbours, mode)
        if (i - 1 >= 0) processCell(i, j - 1, i, j, finish, f, g, h, mc, parents, processed, occupied, neighbours, mode)
        processed[i][j] = true
        nextCell = neighbours.extractMin()//extractMin
        i = nextCell.i
        j = nextCell.j
        if (nextCell.i === finish.i && nextCell.j === finish.j) break     
    }

    // 
    let path = []
    let curCell = {i: nextCell.i, j: nextCell.j, distance: g[nextCell.i][nextCell.j]}
    while (!(curCell.i === start.i && curCell.j === start.j)) {
        path.push(curCell)
        let {i: pi, j: pj} = parents[i][j]// 
        curCell = {i: pi, j: pj, distance: g[pi][pj]}
        i = curCell.i
        j = curCell.j
    } 
    path.push(curCell)
    return path
}