import React from "react"
import '../styles/styles.css'
import BattleQueue from './BattleQueue'
import UnitEffects from './UnitEffects'

import img_skipTurn from './game resources/img/menu/skipTurn.png';
import img_spellBook from './game resources/img/menu/spellBook.png';
import img_cancelSpell from './game resources/img/menu/cancelSpell.png';

import phys from './game resources/img/icons/phys res.png';
import air from './game resources/img/icons/air magic.png';
import water from './game resources/img/icons/water magic.png';
import fire from './game resources/img/icons/fire magic.png';
import earth from './game resources/img/icons/earth magic.png';
import light from './game resources/img/icons/light magic.png';
import dark from './game resources/img/icons/dark magic.png';


export default function BattleMenu(props) {
    let [btnDesc, setBtnDesc] = React.useState("")
    let info = props.displayedUnit
    let {name, img, lvl,
        HP, maxHP, Mana, maxMana, WP, maxWP, AP, MP, IP,
        vit, wis, str, agi, int, will, AC, ini,
        physRes, waterRes, fireRes, airRes, earthRes, lightRes, darkRes,
        x, y, states} = info   

    function makeLogo(img) {
        return <img className="crinfo--table--main--logo" src={img} alt=""/>
    }

    return(
        <div className="battle--menu">
            <div className="battle--menu--buttons-block">
                <img className="battle--menu--button" src={img_skipTurn} alt=" " 
                     onClick={props.finishTurn}
                     onMouseOver={() => setBtnDesc(" ")}
                     onMouseOut={() => setBtnDesc("")}/>
                <img className="battle--menu--button" src={img_spellBook} alt=" " 
                     onClick={props.openSpellBook}
                     onMouseOver={() => setBtnDesc(" ")}
                     onMouseOut={() => setBtnDesc("")}/>
                <img className="battle--menu--button" src={img_cancelSpell} alt=" " 
                     onClick={()=>props.setIsCastingSpell()}
                     onMouseOver={() => setBtnDesc(" ")}
                     onMouseOut={() => setBtnDesc("")}/>
                <div className="battle--menu--btnDesc">{btnDesc}</div>
            </div>      
            <div className="battle--creature-info">
                <table className="crinfo--table--main">
                    <tbody>
                    <tr><td rowSpan="4" className="crinfo--table--main--imgTd"><img className="crinfo--table--main--img" src={img} alt=""/></td><td colSpan="4">{name} {lvl} . <div className="crinfo--table--coords">x: {x} y: {y}</div></td></tr>
                    <tr><td className="main-td-1">HP:</td><td className="main-td-2">{HP} / {maxHP}</td><td className="main-td-3">AP:</td><td className="main-td-3">{AP}</td></tr>
                    <tr><td className="main-td-1">Mana:</td><td className="main-td-2">{Mana} / {maxMana}</td><td className="main-td-3">MP:</td><td className="main-td-3">{MP}</td></tr>
                    <tr><td className="main-td-1">WP:</td><td className="main-td-2">{WP} / {maxWP}</td><td className="main-td-3">IP:</td><td className="main-td-3">{IP}</td></tr>
                    </tbody>
                </table>

                <table className="crinfo--table">
                    <thead><tr><td>Vit</td><td>Wis</td><td>Str</td><td>Agi</td><td>Int</td><td>Wil</td><td>AC</td></tr></thead>
                    <tbody><tr><td>{vit}</td><td>{wis}</td><td>{str}</td><td>{agi}</td><td>{int}</td><td>{will}</td><td>{AC}</td></tr></tbody>
                </table>

                <table className="crinfo--table">
                    <thead><tr><td>{makeLogo(phys)}</td><td>{makeLogo(water)}</td><td>{makeLogo(fire)}</td><td>{makeLogo(air)}</td><td>{makeLogo(earth)}</td><td>{makeLogo(light)}</td><td>{makeLogo(dark)}</td></tr></thead>
                    <tbody><tr><td>{physRes}%</td><td>{waterRes}%</td><td>{fireRes}%</td><td>{airRes}%</td><td>{earthRes}%</td><td>{lightRes}%</td><td>{darkRes}%</td></tr></tbody>
                </table>   
            </div>
            <div className="current-turn">: {props.currentTurn}</div>       
            <BattleQueue queue={props.queue} 
                         units={props.units}
                         currentTurn={props.currentTurn}
                         hoverQueueImg={props.hoverQueueImg} 
                         removeFocus={props.removeFocus}/>
            <UnitEffects displayedUnit={props.displayedUnit}/>
        </div>
    )
}